<template>
  <view class="draw-container">
    <!-- 自定义导航栏 -->
    <view class="custom-navbar">
      <view class="navbar-left">
        <button class="menu-btn" @tap="toggleGameMenu">
          <image class="menu-icon" src="/assets/icons/菜单.png" mode="aspectFit"></image>
        </button>
      </view>
      <view class="navbar-center">
        <text class="navbar-title">绘画游戏</text>
      </view> 
      <view class="navbar-right"></view>
    </view>
    
    <!-- 游戏状态栏 -->
    <view class="game-status">
      <view class="status-item">
        <text class="status-label">时间：</text>
        <text class="status-value time">{{timeLeft}}s</text>
      </view>
      <view class="status-item">
        <text class="status-label">得分：</text>
        <text class="status-value score">{{score}}</text>
      </view>
    </view>

    <!-- 画布区域 -->
    <view v-if="gameStatus !== 'finished'">
      <canvas 
        id="gameCanvas"
        type="2d"
        class="game-canvas"
        @touchstart="onTouchStart"
        @touchmove="onTouchMove"
        @touchend="onTouchEnd"
        style="width: 677rpx; display: block; box-sizing: border-box; left: 0rpx; top: 0rpx">
      </canvas>

      <!-- 绘画工具栏 -->
      <view class="toolbar">
        <view class="tool-group" style="height: 169rpx; display: block; box-sizing: border-box; left: 0rpx; top: 0rpx">
          <view class="word-display" style="width: 408rpx; display: flex; box-sizing: border-box; height: 124rpx;margin-top: 30rpx;margin-left: 50rpx;">
            <text class="word-text">{{currentWord || '点击选词'}}</text>
            <button class="word-btn" @tap="getRandomWord">选词</button>
          </view>
          <button class="tool-btn" @tap="clearCanvas" style="position: relative; left: 227rpx; top: -77rpx; width: 211rpx; display: flex; box-sizing: border-box">清空</button>
        </view>
      </view>
    </view>

    <!-- 聊天区域 -->
    <view class="chat-container">
      <view class="chat-messages" style="height: 261rpx; display: block; box-sizing: border-box">
        <view 
          :class="['message-item', item.type]"
          v-for="(item, index) in messages"
          :key="index">
          
          <view class="message-content" v-if="item.type === 'system'">
            <text class="system-text">{{item.content}}</text>
            <text class="message-time">{{item.time}}</text>
          </view>
          
          <view class="message-content" v-else>
            <text class="user-text">{{item.content}}</text>
            <text class="message-time">{{item.time}}</text>
          </view>
        </view>
        
        <!-- 空状态 -->
        <view class="chat-empty" v-if="messages.length === 0" style="height: 202rpx; display: block; box-sizing: border-box">
          <text class="empty-text">开始聊天吧！</text>
        </view>
      </view>
      
      <!-- 输入框 -->
      <view class="chat-input" v-if="gameStatus === 'drawing'">
        <input 
          class="input-field"
          placeholder="输入你的猜测..."
          v-model="inputText"
          @input="onInputChange"
          confirm-type="send"
          @confirm="sendMessage" />
        
        <button class="send-btn" @tap="sendMessage">
          <text class="send-icon">📤</text>
        </button>
      </view>
    </view>

    <!-- 游戏菜单 -->
    <view class="game-menu" v-if="showGameMenu">
      <view class="menu-overlay" @tap="toggleGameMenu"></view>
      <view class="menu-content">
        <view class="menu-title">游戏菜单</view>
        <button class="menu-btn" @tap="restartGame">重新开始</button>
        <button class="menu-btn" @tap="exitGame">退出游戏</button>
      </view>
    </view>

    <!-- 游戏结果弹窗 -->
    <view class="result-modal" v-if="showResult">
      <view class="modal-content">
        <view class="result-header">
          <text class="result-title">🎉 游戏结束</text>
        </view>
        
        <view class="result-stats">
          <view class="stat-item">
            <text class="stat-label">总分数</text>
            <text class="stat-value">{{gameResult.totalScore}}</text>
          </view>
          
          <view class="stat-item">
            <text class="stat-label">表现评价</text>
            <text class="stat-value">{{gameResult.performance}}</text>
          </view>
        </view>
        
        <view class="result-actions">
          <button class="result-btn secondary" @tap="exitGame">
            返回房间
          </button>
          
          <button class="result-btn primary" @tap="restartGame">
            再来一局
          </button>
        </view>
      </view>
    </view>
  </view>
</template>

<script>
export default {
  data() {
    return {
      timeLeft: 60,
      score: 0,
      gameStatus: 'waiting', // waiting, drawing, finished
      currentWord: '',
      messages: [],
      inputText: '',
      showGameMenu: false,
      showResult: false,
      gameResult: {
        totalScore: 0,
        performance: ''
      },
      canvas: null,
      context: null,
      isDrawing: false,
      lastX: 0,
      lastY: 0
    }
  },
  onLoad() {
    this.initCanvas()
    this.startGame()
  },
  methods: {
    async initCanvas() {
      try {
        const query = uni.createSelectorQuery()
        query.select('#gameCanvas')
          .fields({ node: true, size: true })
          .exec((res) => {
            const canvas = res[0].node
            const ctx = canvas.getContext('2d')
            
            const dpr = uni.getSystemInfoSync().pixelRatio
            canvas.width = res[0].width * dpr
            canvas.height = res[0].height * dpr
            ctx.scale(dpr, dpr)
            
            this.canvas = canvas
            this.context = ctx
            
            // 设置默认样式
            ctx.strokeStyle = '#000000'
            ctx.lineWidth = 2
            ctx.lineCap = 'round'
            ctx.lineJoin = 'round'
          })
      } catch (error) {
        console.error('初始化画布失败:', error)
      }
    },
    startGame() {
      this.gameStatus = 'drawing'
      this.score = 0
      this.timeLeft = 60
      this.startTimer()
    },
    startTimer() {
      const timer = setInterval(() => {
        if (this.timeLeft > 0) {
          this.timeLeft--
        } else {
          clearInterval(timer)
          this.endGame()
        }
      }, 1000)
    },
    onTouchStart(e) {
      if (this.gameStatus !== 'drawing') return
      
      const touch = e.touches[0]
      const rect = this.canvas.getBoundingClientRect()
      this.isDrawing = true
      this.lastX = touch.clientX - rect.left
      this.lastY = touch.clientY - rect.top
    },
    onTouchMove(e) {
      if (!this.isDrawing) return
      
      const touch = e.touches[0]
      const rect = this.canvas.getBoundingClientRect()
      const currentX = touch.clientX - rect.left
      const currentY = touch.clientY - rect.top
      
      this.context.beginPath()
      this.context.moveTo(this.lastX, this.lastY)
      this.context.lineTo(currentX, currentY)
      this.context.stroke()
      
      this.lastX = currentX
      this.lastY = currentY
    },
    onTouchEnd() {
      this.isDrawing = false
    },
    clearCanvas() {
      if (!this.context) return
      this.context.clearRect(0, 0, this.canvas.width, this.canvas.height)
    },
    async getRandomWord() {
      try {
        // 从后端获取随机词语
        // TODO: 实现获取随机词语的逻辑
        this.currentWord = '示例词语'
      } catch (error) {
        console.error('获取随机词语失败:', error)
      }
    },
    onInputChange(e) {
      this.inputText = e.detail.value
    },
    sendMessage() {
      if (!this.inputText.trim()) return
      
      const message = {
        content: this.inputText,
        type: 'user',
        time: new Date().toLocaleTimeString()
      }
      
      this.messages.push(message)
      this.inputText = ''
      
      // TODO: 实现消息发送到服务器的逻辑
    },
    toggleGameMenu() {
      this.showGameMenu = !this.showGameMenu
    },
    restartGame() {
      this.showGameMenu = false
      this.showResult = false
      this.clearCanvas()
      this.startGame()
    },
    exitGame() {
      uni.showModal({
        title: '提示',
        content: '确定要退出游戏吗？',
        success: (res) => {
          if (res.confirm) {
            uni.navigateBack()
          }
        }
      })
    },
    endGame() {
      this.gameStatus = 'finished'
      this.showResult = true
      this.gameResult = {
        totalScore: this.score,
        performance: this.getPerformanceEvaluation()
      }
    },
    getPerformanceEvaluation() {
      if (this.score >= 90) return '神级画手'
      if (this.score >= 70) return '画技高超'
      if (this.score >= 50) return '表现不错'
      return '继续加油'
    }
  }
}
</script>

<style>
.draw-container {
  min-height: 100vh;
  background-color: #f6f6f6;
}

.custom-navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20rpx;
  background-color: #fff;
  box-shadow: 0 2rpx 10rpx rgba(0, 0, 0, 0.1);
}

.menu-btn {
  background: none;
  border: none;
  padding: 0;
  margin: 0;
  line-height: 1;
}

.menu-btn::after {
  display: none;
}

.menu-icon {
  width: 40rpx;
  height: 40rpx;
}

.navbar-title {
  font-size: 32rpx;
  font-weight: bold;
  color: #333;
}

.game-status {
  display: flex;
  justify-content: space-around;
  padding: 20rpx;
  background-color: #fff;
  margin-bottom: 20rpx;
}

.status-item {
  display: flex;
  align-items: center;
}

.status-label {
  font-size: 28rpx;
  color: #666;
}

.status-value {
  font-size: 28rpx;
  font-weight: bold;
  color: #333;
}

.status-value.time {
  color: #f44336;
}

.status-value.score {
  color: #4CAF50;
}

.game-canvas {
  width: 100%;
  height: 600rpx;
  background-color: #fff;
  margin-bottom: 20rpx;
}

.toolbar {
  background-color: #fff;
  padding: 20rpx;
  margin-bottom: 20rpx;
}

.word-display {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 20rpx;
}

.word-text {
  font-size: 32rpx;
  color: #333;
  font-weight: bold;
}

.word-btn {
  background-color: #4CAF50;
  color: #fff;
  font-size: 28rpx;
  padding: 10rpx 30rpx;
  border-radius: 30rpx;
  border: none;
}

.word-btn::after {
  display: none;
}

.tool-btn {
  background-color: #f5f5f5;
  color: #666;
  font-size: 28rpx;
  padding: 10rpx 30rpx;
  border-radius: 30rpx;
  border: none;
}

.tool-btn::after {
  display: none;
}

.chat-container {
  background-color: #fff;
  padding: 20rpx;
}

.chat-messages {
  height: 300rpx;
  overflow-y: auto;
}

.message-item {
  margin-bottom: 16rpx;
}

.message-content {
  display: flex;
  flex-direction: column;
}

.system-text {
  font-size: 24rpx;
  color: #666;
  font-style: italic;
}

.user-text {
  font-size: 28rpx;
  color: #333;
}

.message-time {
  font-size: 22rpx;
  color: #999;
  margin-top: 4rpx;
}

.chat-empty {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.empty-text {
  font-size: 28rpx;
  color: #999;
}

.chat-input {
  display: flex;
  align-items: center;
  gap: 20rpx;
  margin-top: 20rpx;
}

.input-field {
  flex: 1;
  height: 80rpx;
  background-color: #f5f5f5;
  border-radius: 40rpx;
  padding: 0 30rpx;
  font-size: 28rpx;
}

.send-btn {
  width: 80rpx;
  height: 80rpx;
  border-radius: 40rpx;
  background-color: #4CAF50;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  margin: 0;
  border: none;
}

.send-btn::after {
  display: none;
}

.send-icon {
  font-size: 40rpx;
  color: #fff;
}

.game-menu {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 999;
}

.menu-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.4);
}

.menu-content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #fff;
  border-radius: 20rpx;
  padding: 40rpx;
  width: 80%;
  max-width: 600rpx;
}

.menu-title {
  font-size: 36rpx;
  font-weight: bold;
  color: #333;
  text-align: center;
  margin-bottom: 40rpx;
}

.menu-btn {
  width: 100%;
  height: 88rpx;
  border-radius: 44rpx;
  margin-bottom: 20rpx;
  font-size: 32rpx;
  background-color: #f5f5f5;
  color: #333;
  border: none;
}

.menu-btn::after {
  display: none;
}

.menu-btn:last-child {
  margin-bottom: 0;
  background-color: #f44336;
  color: #fff;
}

.result-modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.4);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 999;
}

.modal-content {
  background-color: #fff;
  border-radius: 20rpx;
  padding: 40rpx;
  width: 80%;
  max-width: 600rpx;
}

.result-header {
  text-align: center;
  margin-bottom: 40rpx;
}

.result-title {
  font-size: 36rpx;
  font-weight: bold;
  color: #333;
}

.result-stats {
  margin-bottom: 40rpx;
}

.stat-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20rpx;
}

.stat-item:last-child {
  margin-bottom: 0;
}

.stat-label {
  font-size: 28rpx;
  color: #666;
}

.stat-value {
  font-size: 32rpx;
  font-weight: bold;
  color: #333;
}

.result-actions {
  display: flex;
  gap: 20rpx;
}

.result-btn {
  flex: 1;
  height: 88rpx;
  border-radius: 44rpx;
  font-size: 32rpx;
  border: none;
}

.result-btn::after {
  display: none;
}

.result-btn.primary {
  background-color: #4CAF50;
  color: #fff;
}

.result-btn.secondary {
  background-color: #f5f5f5;
  color: #333;
}
</style> 